 header {
     background-color: #2a3e9d;
     background-color: #00000000;
     backdrop-filter: blur(10px);
     color: #aaf0e5;
     text-align: center;

     position: fixed;
     top: 0;
     left: 0;
     z-index: 2;

     padding: 1rem;
     width: 100vw;
     max-width: 100vw;
     max-height: 90px;

     display: flex;
     align-items: center;
     justify-content: start;
     gap: 1rem;

     box-shadow: 1px 1px 8px #00000052;


     img {
         max-height: 75px;
     }

     span {
         color: #fff;
     }

 }